












































































































































.nf-layout-wrap {
  .nf-sider {
    height: 100vh;
  }
  .nf-header {
    padding: 0;
    background: #fff;
    .ant-col + .ant-col {
      display: flex;
      justify-content: flex-end;
      padding: 0 @nf-padding;
    }
    .trigger {
      height: @nf-header-height;
      padding: 0 @nf-padding;
      font-size: 18px;
      line-height: @nf-header-height;
      cursor: pointer;
      transition: color 0.3s;
      &:hover {
        color: #1890ff;
      }
    }
  }
}

@nf-color-blue: #1890ff;@nf-margin: 20px;@nf-padding: 20px;@nf-header-height: 65px;